header{
    height: 100vh;
    padding-top: 7rem;
    overflow: hidden;
    margin-bottom: 30px;
}
.header_container{
    text-align: center;
    height: 100%;
    position: relative;
}
.CV{
    margin-top: 2.5rem;
    display: flex;
    gap: 1.2rem;
    justify-content: center;
}
.header_socials{
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 0.8rem;
    position: absolute;
    left: 0;
    bottom: 3rem;
    font-size: x-large;
}
.me{
    background: linear-gradient(green,red);
    width: 22rem;
    height: 30rem;
    position: absolute;
    left:calc(50% - 11rem);
    margin-top: 4rem;
    border-radius: 12rem 12rem 0 0;
    overflow: hidden;
    padding: .5rem 1.5rem 1.5rem 1.5rem;
}
.scroll_down{
    position: absolute;
    right: -2.3rem;
    bottom: 5rem;
    transform: rotate(90deg);
    font-weight: 300;
    font-size: 1.5rem;
}
@media screen and (max-width:1024px){
    header{
        height: 68vh;
    }
}
@media screen and (max-width:600px){
    header{
        height: 100vh;
    }
    .header_socials,.scroll_down{
        display: none;
    }
}