#about{
  height: fit-content;
}
.about_container {
  display: grid;
  grid-template-columns: 35% 50%;
  gap: 15%;
}

.about_me {
  width: 100%;
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}
.about_me_img {
  border-radius: 2rem;
  overflow: hidden;
  transform: rotate(10deg);
  transition: all 0.5s linear;
}
.about_me_img:hover {
  transform: rotate(0deg);
}
.about_cards {
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  gap: 1.5rem;
}
.about_card {
  border: 1px solid transparent;
  border-radius: 1rem;
  text-align: center;
  padding: 2rem;
}
.about_card:hover {
  background: transparent;
  border-color: #2c2c6c;
  cursor: pointer;
}
.about_icon {
  font-size: 1.4rem;
  margin-bottom: 1.1rem;
  color: #4db5ff;
}
.about_card h3 {
  font-size: 0.9rem;
}
.about_card small {
  font-size: 0.7rem;
  color: rgba(255, 255, 255, 0.6);
}
.about_content p {
  margin: 2rem 0 2.6rem;
  color: rgba(255, 255, 255, 0.6);
}
/* MEDIA QUERIES (MEDIUM DEVICES) */
@media screen and (max-width: 1024px) {
  .about_container {
    grid-template-columns: 1fr;
    gap: 0;
  }
  .about_me {
    width: 50%;
    margin: 2rem auto 4rem;
  }
  .about_content {
    margin: 1rem 0 1.5rem;
  }
}
/* MEDIA QUERIES (SMALL DEVICES) */
@media screen and (max-width: 600px) {
  .about_me {
    width: 65%;
    margin: 0 auto 3rem;
  }
  .about_cards {
    grid-template-columns: 1fr 1fr;
    gap: 1rem;
  }
  .about_content {
    text-align: center;
  }
  .about_content p {
    margin: 1.5rem 0;
  }
}
